import { useEffect, useState } from 'react';
import Navbar from '../../components/Navbar';
import PostCard from '../../components/PostCard';
import api from '../../utils/api';
import Link from 'next/link';

export default function Dashboard() {
const [posts, setPosts] = useState([]);

useEffect(() => {
fetchPosts();
}, []);

const fetchPosts = async () => {
try {
const res = await api.get('/posts/approved');
setPosts(res.data);
} catch (err) {
console.error(err);
}
};

return (
<> <Navbar /> <div className="container mt-6"> <div className="flex items-center justify-between mb-6"> <h2 className="text-2xl font-bold">Marketplace</h2> <div className="flex space-x-3"> <Link href="/dashboard/create-post" className="btn btn-primary">Create Post</Link> <Link href="/dashboard/orders" className="btn btn-ghost">My Orders</Link> <Link href="/dashboard/inbox" className="btn btn-ghost">Inbox</Link> </div> </div> <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
{posts.map(p => <PostCard key={p._id} post={p} />)} </div> </div>
</>
);
}

